<template>
  <div id="table-card-component">
    <div class="top-container">
      <h3 class="title">{{ title }}</h3>
      <p class="subtitle">{{ subtitle }}</p>
      <div
        v-if="expand && hasData"
        class="expand-button"
        @click="onExpandClick"
      >
        <img src="@/assets/images/icons/png/expand.png" alt="" />
      </div>
    </div>

    <div v-if="hasData" class="table-container">
      <el-table
        :data="data"
        :style="{
          width: 'calc(100% - 60px)',
          height: '600px',
          overflowY: 'scroll'
        }"
        row-class-name="table-row"
        cell-class-name="table-cell"
        :default-sort="{ prop: 'count', order: 'descending' }"
      >
        <el-table-column type="index" width="50"> </el-table-column>
        <el-table-column
          v-for="column in columns"
          :prop="column.prop"
          :key="column.prop"
          :label="column.label"
          :width="column.width || 'auto'"
          :sortable="column.sort"
        >
          <template #default="scope">
            <div v-if="column.prop === 'collection'" class="collection-cell">
              <div class="collection-wrapper" v-if="scope.row.fabric_images">
                <CollectionCard :data="scope.row" cover-mode></CollectionCard>
              </div>
            </div>

            <div v-else-if="column.prop === 'fabric'" class="fabric-cell">
              <img
                :src="formatImageUrl(scope.row.image_url) + '@100-100'"
                alt=""
              />
            </div>

            <div v-else-if="column.prop === 'color'" class="color-cell">
              <div
                class="circle"
                :style="{ background: scope.row.color }"
              ></div>
              <p>{{ scope.row.color }}</p>
            </div>

            <div v-else-if="column.prop === 'ctr'">
              <p>{{ Math.round(scope.row.ctr * 100) + "%" }}</p>
            </div>

            <p v-else>{{ scope.row[column.prop] }}</p>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div v-else class="chart-empty">
      <h3>No Data</h3>
    </div>

    <el-dialog :title="title" v-model="dialogVisible" width="80%">
      <div class="table-container">
        <el-table
          :data="data"
          :style="{ width: 'calc(100% - 60px)' }"
          row-class-name="table-row"
          cell-class-name="table-cell"
        >
          <el-table-column
            v-for="column in columns"
            :key="column.prop"
            :label="column.label"
            :width="column.width || 'auto'"
          >
            <template #default="scope">
              <div v-if="column.prop === 'fabric'" class="fabric-cell">
                <p>{{ scope.row.fabric.name }}</p>
                <img :src="scope.row.fabric.image" alt="" />
              </div>
              <p v-else>{{ scope.row[column.prop] }}</p>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, PropType, computed, toRefs } from "vue";
import { Column } from "@/constants/element";
import CollectionCard from "@/components/CollectionCard.vue";
import { formatImageUrl } from "@/utils";

const Component = defineComponent({
  name: "TableCard",
  components: { CollectionCard },
  props: {
    title: {
      type: String,
      default: ""
    },
    subtitle: {
      type: String,
      default: ""
    },
    columns: {
      type: Array as PropType<Column[]>,
      default: () => []
    },
    data: {
      type: Array,
      default: () => []
    },
    expand: {
      type: Boolean,
      default: false
    }
  },
  setup(props) {
    const { data } = toRefs(props);

    const dialogVisible = ref(false);
    const hasData = computed(() => {
      return data.value.length;
    });

    const onExpandClick = () => {
      dialogVisible.value = true;
    };

    return {
      dialogVisible,
      formatImageUrl,
      hasData,

      onExpandClick
    };
  }
});

export default Component;
</script>

